import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  TextField,
  Button,
  Typography,
  Box,
  Paper,
  Slider,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Switch,
  FormControlLabel,
  SelectChangeEvent,
} from '@mui/material';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import { useSettings, Settings } from '../context/SettingsContext';

const HomePage = () => {
  const navigate = useNavigate();
  const [text, setText] = useState('');
  const { settings, updateSettings } = useSettings();

  const handleStart = () => {
    if (text.trim()) {
      localStorage.setItem('trainingText', text);
      navigate('/trainer');
    }
  };

  const handleWordsAtOnceChange = (e: SelectChangeEvent<number>) => {
    const value = e.target.value as Settings['wordsAtOnce'];
    updateSettings({ wordsAtOnce: value });
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4, mb: 4 }}>
        <Box sx={{ textAlign: 'center', mb: 4 }}>
          <RemoveRedEyeIcon sx={{ fontSize: 60, color: 'primary.main', mb: 2 }} />
          <Typography 
            variant="h3" 
            component="h1" 
            gutterBottom 
            sx={{
              fontWeight: 'bold',
              background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent'
            }}
          >
            Тренажер швидкого читання
          </Typography>
          <Typography 
            variant="h5" 
            color="textSecondary"
            gutterBottom
          >
            Око в центрі
          </Typography>
        </Box>

        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <TextField
            fullWidth
            multiline
            rows={10}
            variant="outlined"
            label="Введіть текст для читання"
            value={text}
            onChange={(e) => setText(e.target.value)}
            sx={{ mb: 2 }}
          />
        </Paper>

        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            Налаштування
          </Typography>
          
          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
              Швидкість читання (слів за хвилину): {settings.wordsPerMinute}
            </Typography>
            <Slider
              value={settings.wordsPerMinute}
              onChange={(_, value) => updateSettings({ wordsPerMinute: value as number })}
              min={100}
              max={1000}
              step={10}
            />
          </Box>

          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
              Розмір шрифту: {settings.fontSize}
            </Typography>
            <Slider
              value={settings.fontSize}
              onChange={(_, value) => updateSettings({ fontSize: value as number })}
              min={12}
              max={48}
              step={2}
            />
          </Box>

          <Box sx={{ mb: 3 }}>
            <FormControl fullWidth>
              <InputLabel>Кількість слів за раз</InputLabel>
              <Select<Settings['wordsAtOnce']>
                value={settings.wordsAtOnce}
                onChange={handleWordsAtOnceChange}
                label="Кількість слів за раз"
              >
                {[1, 2, 3, 4, 5, 6, 7].map((num) => (
                  <MenuItem key={num} value={num}>
                    {num}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Box>

          <Box sx={{ mb: 3 }}>
            <FormControl fullWidth>
              <InputLabel>Шрифт</InputLabel>
              <Select
                value={settings.font}
                onChange={(e) => updateSettings({ font: e.target.value as string })}
                label="Шрифт"
              >
                <MenuItem value="Roboto">Roboto</MenuItem>
                <MenuItem value="Arial">Arial</MenuItem>
                <MenuItem value="Times New Roman">Times New Roman</MenuItem>
              </Select>
            </FormControl>
          </Box>

          <Box sx={{ mb: 3 }}>
            <FormControl fullWidth>
              <InputLabel>Режим читання</InputLabel>
              <Select
                value={settings.mode}
                onChange={(e) => updateSettings({ mode: e.target.value as 'normal' | 'sprint' | 'marathon' })}
                label="Режим читання"
              >
                <MenuItem value="normal">Звичайний</MenuItem>
                <MenuItem value="sprint">Спринт</MenuItem>
                <MenuItem value="marathon">Марафон</MenuItem>
              </Select>
            </FormControl>
          </Box>

          <Box sx={{ mb: 3 }}>
            <FormControlLabel
              control={
                <Switch
                  checked={settings.showFixationPoint}
                  onChange={(e) => updateSettings({ showFixationPoint: e.target.checked })}
                />
              }
              label="Показувати точку фіксації"
            />
          </Box>
        </Paper>

        <Button
          variant="contained"
          color="primary"
          fullWidth
          size="large"
          onClick={handleStart}
          disabled={!text.trim()}
          sx={{ height: 56 }}
        >
          Почати тренування
        </Button>
      </Box>
    </Container>
  );
};

export default HomePage;
